define('meetup/helpers/code-block', ['exports', 'ember'], function (exports, Ember) {

    'use strict';

    exports['default'] = Ember['default'].Handlebars.makeBoundHelper(function (str, lang) {
        return !!str ? new Ember['default'].Handlebars.SafeString("<div class=\"code\">" + prettyPrintOne(str, lang) + "</div>") : str;
    });

});